import { Component } from 'react';
import Link from 'next/link';

export default class Layout extends Component {
    render() {
        return (
            <div>
                <Header>
                    <Link href="/">
                        <a>Podcasts</a>
                    </Link>
                </Header>
                <style jsx>{`
                    header {
                            color: #fff;
                            background: #55E6C1;
                            padding: 15px;
                            text-align: center;
                            font-weight: bold;
                        }
                    header a {
                        color: #fff;S
                        }
                `}</style>
                <style jsx global>{`
                    body {
                        margin: 0;
                        background: white;
                        font-family: system-ui;
                    }
                `}</style>
            </div>
        )
    }
}


